body {
  background-image: linear-gradient(
    to bottom right,
    rgba(117, 109, 113, 0.3),
    rgba(161, 61, 119, 0.3),
    rgba(31, 13, 25, 0.3)
  );
}
.Loader {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border: 10px solid #0f0f0f9f;
  border-radius: 50%;
  border-top: 10px solid #02010a;
  width: 100px;
  height: 100px;
  animation: spin 1.5s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.Center {
  margin-left: auto;
  text-align: center;
  margin-right: auto;
}
.Title {
  text-shadow: 0 0 2px #8b8686, 0 0 3px #143b42;
  color: rgb(41, 170, 24);
  animation-name: header;
  animation-duration: 5s;
  animation-delay: 1s;
  animation-iteration-count: infinite;
}
@keyframes header {
  from {
    color: rgba(14, 116, 28, 0.884);
  }
  to {
    color: rgba(29, 12, 128, 0.884);
  }
}
